class PointClass {
  constructor(x, y) {
    this.type = 'point'
    this.x = x
    this.y = y
  }

  /**
   * 
   * @param {sugar.Point} that
   * @return {sugar.Point} new PointClass
   */
  add(that) {
    return new PointClass(this.x + that.x, this.y + that.y)
  }

  /**
   * 
   * @param {sugar.Point} that
   * @return {sugar.Point} thisArg
   * @chainable
   */
  addEquals(that) {
    this.x += that.x
    this.y += that.y
    return this
  }

  /**
   * 
   * @param {Number} scalar
   * @return {sugar.Point} new PointClass
   */
  scalarAdd(scalar) {
    return new PointClass(this.x + scalar, this.y + scalar)
  }

  /**
   * 
   * @param {Number} scalar
   * @return {sugar.Point} thisArg
   */
  scalarAddEquals(scalar) {
    this.x += scalar
    this.y += scalar
    return this
  }

  /**
   * 
   * @param {sugar.Point} that
   * @return {sugar.Point} new PointClass
   */
  subtract(that) {
    return new PointClass(this.x - that.x, this.y - that.y)
  }

  /**
   * 
   * @param {sugar.Point} that
   * @return {sugar.Point} thisArg
   * @chainable
   */
  subtractEquals(that) {
    this.x -= that.x
    this.y -= that.y
    return this
  }

  /**
   * 
   * @param {Number} scalar
   * @return {sugar.Point}
   */
  scalarSubtract(scalar) {
    return new PointClass(this.x - scalar, this.y - scalar)
  }

  /**
   * 
   * @param {Number} scalar
   * @return {sugar.Point} thisArg
   */
  scalarSubtractEquals(scalar) {
    this.x -= scalar
    this.y -= scalar
    return this
  }


  multiply(scalar) {
    return new PointClass(this.x * scalar, this.y * scalar)
  }

  multiplyEquals(scalar) {
    this.x *= scalar
    this.y *= scalar
    return this
  }

  divide(scalar) {
    return new PointClass(this.x / scalar, this.y / scalar)
  }

  divideEquals(scalar) {
    this.x /= scalar
    this.y /= scalar
    return this
  }

  /**
   * true
   * @param {sugar.Point} that
   * @return {Boolean}
   */
  eq(that) {
    return (this.x === that.x && this.y === that.y)
  }

  /**
   * true
   * @param {sugar.Point} that
   * @return {Boolean}
   */
  lt(that) {
    return (this.x < that.x && this.y < that.y)
  }

  /**
   * true
   * @param {sugar.Point} that
   * @return {Boolean}
   */
  lte(that) {
    return (this.x <= that.x && this.y <= that.y)
  }

  /**

   * true
   * @param {sugar.Point} that
   * @return {Boolean}
   */
  gt(that) {
    return (this.x > that.x && this.y > that.y)
  }

  /**
   * true
   * @param {sugar.Point} that
   * @return {Boolean}
   */
  gte(that) {
    return (this.x >= that.x && this.y >= that.y)
  }


  lerp(that, t) {
    if (typeof t === 'undefined') {
      t = 0.5
    }
    t = Math.max(Math.min(1, t), 0)
    return new PointClass(this.x + (that.x - this.x) * t, this.y + (that.y - this.y) * t)
  }

  /**
   * 
   * @param {sugar.Point} that
   * @return {Number}
   */
  distanceFrom(that) {
    var dx = this.x - that.x,
      dy = this.y - that.y
    return Math.sqrt(dx * dx + dy * dy)
  }

  /**
   * 
   * @param {sugar.Point} that
   * @return {sugar.Point}
   */
  midPointFrom(that) {
    return this.lerp(that)
  }

  /**
   * 
   * @param {sugar.Point} that
   * @return {sugar.Point}
   */
  min(that) {
    return new PointClass(Math.min(this.x, that.x), Math.min(this.y, that.y))
  }

  /**
   * 
   * @param {sugar.Point} that
   * @return {sugar.Point}
   */
  max(that) {
    return new PointClass(Math.max(this.x, that.x), Math.max(this.y, that.y))
  }

  /**
   * 
   * @return {String}
   */
  toString() {
    return this.x + ',' + this.y
  }

  /**
   * x/y
   * @param {Number} x
   * @param {Number} y
   * @chainable
   */
  setXY(x, y) {
    this.x = x
    this.y = y
    return this
  }

  /**
   * x
   * @param {Number} x
   * @chainable
   */
  setX(x) {
    this.x = x
    return this
  }

  /**
   * y
   * @param {Number} y
   * @chainable
   */
  setY(y) {
    this.y = y
    return this
  }

  /**
   * x/y
   * @param {sugar.Point} that
   * @chainable
   */
  setFromPoint(that) {
    this.x = that.x
    this.y = that.y
    return this
  }

  /**
   * xy
   * @param {sugar.Point} that
   */
  swap(that) {
    var x = this.x,
      y = this.y
    this.x = that.x
    this.y = that.y
    that.x = x
    that.y = y
  }

  /**
   * 
   * @return {sugar.Point}
   */
  clone() {
    return new PointClass(this.x, this.y)
  }
}

module.exports = PointClass
